import VueRouter from "vue-router";
import PhoneBook from "./pages/PhoneBook.vue";
import Contact from "./pages/Contact.vue";

export default new VueRouter({
  routes: [
    {
      path: "/ContactBook/",
      component: PhoneBook
    },
    {
      path: "/ContactBook/contact/:id",
      component: Contact
    }
  ],
  mode: "history"
});



